@import "../component.scss";

.main-wrap{
	.section{
		h2{
			position: absolute;
			left: 50%;
			color: #fff;
			text-align: center;
			line-height: 1.5;

		}
	}

	.section-visual{
		position: relative;
		.slide{
			&:before{
				background-color: rgba(0,0,0,.2);
			}
			& > div{
				height: 100%;
				background-position: 50% 50%;
				background-size: cover;
			}
			h2{
				top: 50%;
				transform: translate(-50%, -50%);
				font-size: 30px;
				letter-spacing: -.75px;
				text-shadow: 0px 0px 60px rgba(0, 0, 0, 0.66);
				strong{
					font-size: 62px;
				}
			}
			img{
				max-width: none;
				object-fit: cover;
				object-position: 50% 50%;
			}
			video {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	.section-activity{
		position: relative;
		height: 791px;
		.slide{
			&:before{
				background-color: #ededed;
			//	background-color: rgba(51, 71, 110, 0.8);
			}
			&-1{
			//	background-image: url('../../resources/images/main-activity-bg-1.jpg');
			}
			&-2{
			//	background-image: url('../../resources/images/main-activity-bg-1.jpg');
			}
			&-3{
			//	background-image: url('../../resources/images/main-activity-bg-2.jpg');
			}

			h2{
				top: 110px;
				transform: translateX(-50%);
				font-size: 48px;
				letter-spacing: -.5px;
				color: #2a2a2a;
				time{
					display: block;
					padding-top: 15px;
					font-size: 16px;
				}
			}
			.content{
				padding-top: 265px;
			}

		}
		.slick-arrow{
			border-top: 3px solid #cbcbcb;
			border-left: 3px solid #cbcbcb;
		}

		.program{
			&-img{
				width: 416px;
				&:before{
					padding-bottom: 94.71%;
				}
			}
			&-info{
				height: 394px;
				padding-top: 20px;
				padding-left: 465px;
			}
			&-title{
				margin-bottom: 20px;
				font-size: 36px;
				strong{
					font-weight: 400;
				}
			}
			&-desc{
				max-height: 48px;
				margin-bottom: 35px;
			}
			&-state{
				strong{
					display: block;
					font-weight: 700;
					&:after{
						display: none;
					}
				}
				span.period{
					font-size: 18px;
				}
			}
			.btn{
				right: auto;
				bottom: 61px;
				left: 465px;
				width: 220px;
			}
		}

		.program-list{
			position: relative;
			width: 1300px;
			margin: 0 auto;
		}

		.program-list.double{
			@include clear;
			li{
				float: left;
				width: 50%;
			}
			.program{
				&-img{
					width: 250px;
					&:before{
						padding-bottom: 120%;
					}
				}
				&-info{
					height: 300px;
					padding-left: 290px;
				}
				&-title{
					margin-bottom: 20px;
					font-size: 30px;
					font-weight: 400;
					strong{
						font-weight: 400;
					}
				}
				&-desc{
					margin-bottom: 20px;
				}
				.btn{
					bottom: 3px;
					left: 290px;
				}
			}
		}

		.volunteer-activities{
			@include clear;
			position: relative;
			width: 1300px;
			margin: 0 auto;
			li{
				position: relative;
				float: left;
				width: 416px;
				margin-left: 25px;
				&:first-child{
					margin-left: 0;
				}
				.title{
					position: absolute;
					width: 100%;
					color: #2a2a2a;
					letter-spacing: -.5px;
					&:before{
						content: '';
						position: absolute;
						display: inline-block;
						height: 1px;
						background-color: rgba(255, 255, 255, .3);
					}
					strong{
						display: block;
						margin-left: 100px;
						font-size: 24px;
						font-weight: 400;
					}
					.count{
						font-size: 48px;
					}
				}
			}
			.volunteer{
				.title{
					top: 147px;
					right: 0;
					&:before{
						top: 18px;
						width: 273px;
					}
					strong{
						padding-top: 10px;
						margin-right: 50px;
						text-align: right;
						font-weight: 400;
					}
					.count{
						position: absolute;
						top: -100%;
						right: -15px;
						transform: rotate(90deg) translate(100%, 100%);
						transform-origin: 100% 100%;
					}
				}
				.photo-lg{
					margin-bottom: 65px;
				}
				.photo-sm{
					float: right;
					margin-right: 50px;
				}
			}
			.volunteering-time{
				.title{
					top: 295px;
					text-align: right;
					strong{
						position: absolute;
						top: 60px;
						right: 0;
					}
				}
			}
			.beneficiary{
				padding-top: 133px;
				.title{
					top: 25px;
					&:before{
						right: 0;
						bottom: 17px;
						width: 215px;
					}
					.count{
						font-size: 60px;
						line-height: 1;
					}
				}
				.photo-lg{
					margin-bottom: 19px;
				}
			}
		}

		.slide-controls{
			position: absolute;
			bottom: 45px;
			width: 100%;
			max-width: 1300px;
			margin: 0 auto;
			text-align: right;
			.slide-paging{
				color: #2a2a2a;
				font-size: 24px;
				font-weight: 400;
				strong{
					font-size: 49px;
					font-weight: 400;
				}
			}
		}
	}

	.section-news{
		position: relative;
		height: 1303px;
		box-sizing: border-box;
		background-size: cover;
		background-position: 50% 50%;
		background-image: url('../../resources/images/main-news-bg.jpg');
		&:before{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			display: block;
			background-color: rgba(222, 222, 222, 0.9);
		}
		h2{
			top: 105px;
			transform: translateX(-50%);
			font-size: 48px;
			letter-spacing: -.5px;
			color: #000;
		}
		.news-list{
			@include clear;
			position: relative;
			width: 1300px;
			margin: 0 auto;
			padding-top: 203px;
			li{
				position: relative;
				float: left;
				width: 416px;
				height: 444px;
				&.news-2,
				&.news-5{
					margin: 0 26px;
				}
				.article{
					position: absolute;
					width: 100%;
					height: 533px;
					box-sizing: border-box;
					background-color: #fff;
					.thumb{
						position: absolute;
						top: 0;
						bottom: 0;
						left: 0;
						width: 100%;
						text-align: center;
						span{
							position: absolute;
							display: block;
							right: 0;
							bottom: 0;
							left: 0;
						}
					}
					.title{
						position: relative;
						margin: 40px 20px 0 44px;
						line-height: 1.2;
						color: #333;
						font-size: 30px;
					}
					.category{
						display: inline-block;
						min-width: 100px;
						height: 20px;
						margin-bottom: 35px;
						line-height: 20px;
						color: #fff;
						font-size: 14px;
						text-align: center;
						border-left: 4px solid #a5cf4f;
						background-color: #0061a0;
					}
				}
				&.news-4,
				&.news-5,
				&.news-6{
					.article {
						bottom: 0;
					}
				}
				&:nth-child(even){
					.article{
						height: 323px;
					}
				}
				&.news-2{
					.article{
						background-color: #535353;
						.title{
							color: #fff;
						}
					}
				}
				&.news-4{
					.article{
						.thumb{
							&:after{
								content: '';
								position: absolute;
								top: 0;
								right: 0;
								bottom: 0;
								left: 0;
								display: block;
								background-color: rgba(0, 97, 160, .68);
							}
						}
						.title{
							color: #fff;
						}
					}
				}
			}
		}
	}

	.section-service{
		height: 212px;
		text-align: center;
		background-color: #5d5d5d;
		ul{
			width: 1300px;
			height: 100%;
			margin: 0 auto;
			li{
				float: left;
				width: 162px;
				height: 100%;
				text-align: center;
				color: #fff;
				font-size: 14px;
				line-height: 1.2;
				letter-spacing: -.5px;
				a{
					display: block;
					height: 100%;
					&:hover{
						background-color: #3e3e3e;
					}
				}
			}
		}
	}
}








